
body{
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Frutiger, "Frutiger Linotype", "Dejavu Sans", "Helvetica Neue", Arial, sans-serif;
  color: #2c2c2c;
}
*{
  box-sizing: border-box;
}

/* styling menu left */
.menu-left{
  position: fixed;
  top:0;
  left: 0;
  width: 280px;
  height: 100vh;
  z-index: 1;
  &__logo{
    width: 100%;
    height: 102px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__input-find-wrap{
    position: relative;
    margin-top: 28px;
    margin-left: 20px;
    margin-right: 10px;
    img{
      position: absolute;
      top:12px;
      left:10px;
    }
  }
  &__input-find{
    
    width: 250px;
    padding-left: 31px;
    padding-right: 20px;
    padding-top: 4px;
    font-size: 12px;
    font-weight: 300;
    border: none;
    background-color: #ececec;
    color: #919191; 
    height: 35px;
  }
  &-select{
    display: flex;
    width: 100%;
    flex-direction: column;
    
    &__item{
      text-decoration: none;
      font-weight: 600; 
      font-size: 16px;  
      
      
      display: flex;
      flex-direction: column;
      
      color: #000;
      width: 100%;
      border-top: 1px solid #e9e9e9;
      position: relative;
      color: #000;

      
      &-icon{
        position: absolute;
        top:10px;
        left: 20px;
        
      }
      &-link{
        width: 100%;
        padding-left: 63px;
        text-decoration: none;
        font-weight: 600; 
        font-size: 16px;
        display: flex;
        align-items: center;
        height: 41px;
        width: 100%;
        color: #000;
      }
      .menu-api{
        display: none;
      }
      &:last-child{
        border-bottom: 1px solid #e9e9e9;
      }
      &--down{

        &:after{
          content:url("../src/arrow.svg");
          position: absolute;
          top:9px;
          right: 17px;
          transform: rotate(-45deg);

        }
      }
    }

    
    
  }


}

.menu-left-select__item--active.menu-left-select__item{
  .menu-api{
       display: flex;
      }
    
}
.menu-left-select__item--active{

        .menu-left-select__item-link{
          background-color: #f7f7f8;
        }
         .menu-left-select__item-icon{
          position: absolute;
          top:10px;
          left: 20px;
          
        }
      }



/* styling menu top */
.menu-top{
 z-index: 1;
  position: fixed;
  top:0;
  left: 280px;
  width: calc(100vw - 280px) ;
  
  background-color: #ececec;
  &-select-sub{

    display: flex;
    align-items: center;
    height: 50px;
    width: 100%;
    background-color: #fff;
    
    padding-left: 30px;
    box-shadow: 0 0 1em #e5e5e5;
    &__item{
      font-size: 14px;
      font-weight: 400;
      color: #000;
      display: flex;
      align-items: center;
      text-decoration: none;
      padding-left: 14px;
      padding-right: 14px;
      margin-right: 14px;
      position: relative;
      height: inherit;
      &:hover:after{
        content:'';
        width:100%;
        height: 4px;
        border-radius: 2px;
        position: absolute;
        bottom: -1px;
        left: 0;
        background-color: #51a0d5;
      }
    }
  }
  &-select{
    display: flex;
    margin-left: 20px;
    margin-top: 28px;
    &__item{
      text-decoration: none;
      position: relative;
      margin-left: 10px;
      margin-bottom: 7px;
      margin-right: 10px;
      padding-right: 25px;
      padding-left: 25px;
      display: flex;
      height: 27px;
      align-items: center;
      font-size: 16px;
      font-weight: 400;
      color: #000;

      &:hover{
        background-color: #fff; 
        border-radius:  20px;   
      }
    }
  }
    
    
}
.content{
  position: fixed;
  top:62px;
  left: 280px;
  width: calc(100vw - 280px);
  height: calc(100vh - 62px);
  overflow: scroll;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #ececec;
}
.content2{
  position: absolute;
  top:0;
  width: 100%;
  
  background-color: #fefefe;
  margin:0;
}
.container{
  max-width: 1000px;
}

h1{
  font-size: 50px;
  text-align: center;
  line-height: 1.1em;
  color: #2c2c2c;
  font-weight: 300;
}
h2{
  font-size: 16px;
  font-weight: 700;
  padding-left: 30px;
}
h4{
  font-size: 20px;
  font-weight: 700;
}
p{
  font-size: 16px;
  font-weight: 400;
  padding-left: 30px;
}

.sticky {
  position: fixed;
  top: 0;
  width: 234px;
}
.table-parameters{
  border: 1px solid #000;
  margin-top: 22px;
  border-collapse: collapse;
  margin-left: 15px;
  tr{
    border-bottom: 1px solid #e8e8e8;
  }
  tr:nth-child(1){
    background-color: #f7f7f8;
    border-bottom: 1px solid #000;
  }
  tr:nth-child(7){
    
    border-bottom: none;
  }
  tr.tr-result{
    background-color: #f7f7f8;
    border-bottom: 1px solid #000;
  }
  tr:last-child{
    border-bottom: none;
  }
  th {
    font-size: 16px;
    font-weight: 400;
    color: #2c2c2c;
    height: 42px;
    vertical-align: center;
  }

  td{
    font-size: 14px;
    font-weight: 400;
    color: #2c2c2c;
    text-align: center;
    padding:13px 0;
    vertical-align: center;
  }
  td:nth-child(1) { 
    width: 40px; 
  }
  td:nth-child(2) {
   width: 110px; }
  td:nth-child(3) { 
    width: 114px; 
  }
  td:nth-child(4) {
   width: 350px; 
   text-align: left;

 }
  td:nth-child(5) { 
    width: 135px; 

  }
}
ul li{
  list-style: none;
  position: relative;
  padding-left: 15px;
  &:before{
    content:'';
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background-color: #bdbdbd;
    position: absolute;
    top:50%;
    left: 0px;
    transform: translate(0,-50%);
  }
}
.title-block{
  box-sizing: border-box;
  background-color: #fff;
  box-shadow: 0 0 1em #f2f2f2;
  padding: 44px 15px 35px 28px;
  position: relative;
  &--small.title-block{
    padding: 24px 15px 15px 32px; 
    margin-top: 32px;
    &:after{
      height: 50px;
    }
    
  }
  &:after{
    content:'';
    width:4px;
    height: 119px;
    background-color: #51a0d5;
    border-radius: 2px;
    position: absolute;
    top:50%;
    right: 0;
    transform: translate(50%,-50%);
  }
  &__title{
    font-size: 30px;
    font-weight: 700;
  }
  &__text{
    font-size: 18px;
    font-weight: 400;
  }
}

.btn-select-table{
  background-color: #fff;
  min-height: 35px;
  padding-left: 30px;
  padding-right: 35px;
  font-size: 16px;
  font-weight: 400;
  border-radius: 20px;
  border: none;
  margin-right: 10px;
  margin-left: 10px;
  margin-top: 34px;
  box-shadow: none;
  outline: none;
  &:hover{
    background-color: #f7f7ff;
  }
  &--active{
    background-color: #34495e;
    color:#fff;
    &:hover{
      color:#000;
    }
  }

}
.link{
  
  max-height: 200px;
  background-color: #f7f7f8;
  margin-left: 15px;
  padding: 15px;
  overflow: scroll;
  overflow-x: hidden;
  &--noScroll{
    overflow: hidden;
  }
  a{
    font-size: 14px;
    color: #51a0d5;
    text-decoration: none;
  }
}
.tabs-request{
  display: flex;
  margin-left: 15px;
  &__item{
    cursor: pointer;
    background-color:#fff;
    padding-top: 4px;
    color: #51a0d5;
    font-weight: 400;
    font-size: 16px;
    height: 35px;
    width: 12%;
    box-sizing: border-box;
    margin-right: 20px;
    border: none;
    box-shadow: none;
    border-bottom: 4px solid #e8e8e8;
    outline: none;
    &:hover{
      background-color: #f7f7f8;
      border-radius: 20px;
      border: none;
      padding-top: 0px;
    }
    &--modify{
      color: #fff;
      background-color: #51a0d5;
      border-radius: 20px;
      border: none;
      padding-top: 0px;
      &:hover{
        color: #51a0d5;
      }
    }
  }
}


.response-block{
  margin-left: 15px;
  padding: 20px 15px;
  height: 414px;
  overflow: scroll;
  overflow-x: hidden;
  background-color: #f7f7f8;
}
.start{
  display: flex;
  justify-content: center;
  width: 80%;
  margin: 55px auto;
  &__img{
    width: 30%;
  }
  &__text{
    width: 70%;
  }
}
.start-options{
  display: flex;
  justify-content: space-between;
  &__item{
    width: 27%;
    font-size: 18px;
    font-weight: 400;
    text-align: center;
    position: relative;
    padding-top: 30px;
    &:after{
      content:'';
      background-color: #bdbdbd;
      width: 15px;
      height: 15px;
      border-radius: 10px;
      position: absolute;
      top:0;
      left: 50%;
      transform: translate(-50%);
    }
    &:not(:last-child):before{
      content:'';
      width: 100%;
      height: 0;
      border-bottom: 5px dotted #d7d4d4;
      position: absolute;
      top:5px;
      left: 67%;
      
    }
  }
}
.price{
  margin:40px 15px 64px 28px;
 
  padding:20px 12px 16px 16px;
  background-color: #f4f4f4;
  &__choose{
    
    display: flex;
    &-item{
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 16px;
      font-weight: 400;
      height: 44px;
      background-color: #ededed;
      &:hover{
        background-color: #ededed;
      }
      &--active{
        color: #fff;
        background-color: #51a0d5;
      }
    }
  }
}
.subscribe{
  width: 100%;
  
  margin-top: 15px;
  margin-right: 15px;
  
  border-collapse: collapse;
  th{
     color: #000;
    font-size: 18px;
    font-weight: 700;
    position: relative;
     &:not(:first-child):after{
      content: '';
      width: 50px;
      height: 4px;
      border-radius: 2px;
      background-color: #51a0d5;
      position: absolute;
      bottom:20px;
      left: 50%;
      transform: translate(-50%);
     }
      &:nth-child(odd){
      background-color: #ededed;
    }
    &:nth-child(even){
      background-color: #f8f8f8;
    }
    &:first-child{
      background-color: #f4f4f4;
    }
    
  }
  tr{
    height: 57px;
    text-align: center;
    vertical-align: center;
    border-bottom: 1px solid #e8e8e8;
    &:nth-child(n+10){
      border-bottom: none;
      td{
        font-weight: 700;
      }
    }
  }
  tr:first-child{
    height:71px;
    
  }
  
  
  td{
    color: #000;
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 400;
    width: 20%;
    &:nth-child(odd){
      background-color: #ededed;
    }
    &:nth-child(even){
      background-color: #f8f8f8;
    }
    &:first-child{
      background-color: #f4f4f4;
    }
    
  }

}
.btn-subscribe{
  border: none;
  box-shadow: none;
  width: 120px;
  height: 40px;
  font-size: 16px;
  font-weight: 700;
  background-color: #f39c12;
  border-radius: 20px;
  &:hover{
    background-color: #ff8b00;
  }
}
.prPartnerWrTitle{
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 20px;
}
.prPartnerWrLogo{
  display: flex;
  justify-content: center;
  margin-bottom: 23px;
}
.prPayments{
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: space-between;
  margin-bottom: 20px;
  img{
    width: auto;
    height: 30px;
  }
}
.prPartnerText{
  font-size: 16px;
  margin-bottom: 27px;
  a{
    font-size: 16px;
    text-decoration: none;
    color: #74a2dd;
  }
}
.prPaymentsAlso{
  font-size: 22px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 11px;
}
.imgs{
display: flex;
justify-content: center;

  img{
    width: auto;
    height: 25px;
    margin-right: 18px;
  }
}

.menu-api{
  
  display: flex;
  align-self: flex-start;
  flex-direction: column;
  width: 100%;
  margin-left: 30px;
  margin-top: 15px;
  &__item{
    text-decoration: none;

    font-weight: 400;
    font-size: 14px;
    color: #2c2c2c;
    padding-left: 15px;
    height: 41px;
    width: 100%;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    &:hover{
      padding-left:20px;
    }
    &--active{

      position: relative;
      border-radius: 2px;
      padding-left: 11px;
      padding-left:20px;
      &:before{
        content:'';
        height: 100%;
        width: 4px;
        border-radius: 2px;
        background-color: #51a0d5;
        position: absolute;
        top:0;
        left: -10px;
      }
    }
  }
}



